import request from '@/utils/request'

export default {
  getOrder (mode, obj) {
    return request.get('/checkout/order', {
      params: {
        mode,
        delivery: 10,
        couponId: 0,
        isUsePoints: 0,
        ...obj
      }
    })
  },
  buyOrder (mode, obj) {
    request.post('/checkout/submit', {
      mode,
      delivery: 10,
      couponId: 0,
      isUsePoints: 0,
      ...obj
    })
  },
  getOrderList (dataType, page) {
    return request.get('/order/list', {
      params: {
        dataType,
        page
      }
    })
  }
}
